<template>
    <div>

        <shead :headid="$route.query.headid"></shead>

        <div v-if="!over" class="round-block wrap">
                <div class="search-title">
                    <h2>订单付款</h2>
                    <div class="description">
                    </div>
                </div>

                <div style="margin:22px 30px; ">
                    <el-steps :space="200" :active="2" finish-status="success">
                        <el-step title="下单"></el-step>
                        <el-step title="付款"></el-step>
                        <el-step title="派送"></el-step>
                        <el-step title="收货评价"></el-step>
                    </el-steps>
                </div>
                <div class="shaddr-wrap">
                    <div class="logo"><i class="fa fa-location-arrow"></i></div>
                    <div v-if="receaddress!=null" class="bd">

                        <div class="contact">
                            {{receaddress.mobile}}
                            {{receaddress.shr}}
                        </div>
                        <div class="addinfo">{{receaddress.addinfo}}
                            <span>{{receaddress.postcode}}</span>
                        </div>

                    </div>

                </div>
                <table border="1" class="smart-table">
                    <tr>
                        <td class="tlabel">订单编号</td>
                        <td>{{order.ddno}}</td>
                        <td class="tlabel">下单时间:</td>
                        <td>
                            {{order.createtime}}

                        </td>
                    </tr>
                </table>
                <table width="100%" border="0" cellspacing="0" cellpadding="0" class="ui-record-table">


                    <thead>

                    <tr>
                        <th width="300px">名称</th>
                        <th>价格</th>
                        <th>数量</th>
                        <th>状态</th>
                    </tr>
                    </thead>
                    <tbody>

                    <tr v-for="od in order.orderDetail">
                        <td width="300px;">
                            <div style="display: flex;flex-direction:row;padding:5px 20px; ">
                                <img width="80" height="80" :src="hostHead+od.images[0]"/>
                                <div style="margin-left: 18px;">{{od.spname}}</div>
                            </div>
                        </td>
                        <td>{{od.price}}元</td>
                        <td>{{od.count}}</td>
                        <td>
                            <span v-if="od.state==1">待付款</span>
                            <span v-if="od.state==2">已付款</span>
                            <span v-if="od.state==3">
                                            物流号:{{od.wlno}}
                                        </span>
                            <span v-if="od.state==4">已完成</span>
                        </td>
                    </tr>

                    </tbody>
                </table>

                <table border="1" class="smart-table" >
                    <tr>
                        <td width="10%"  class="tlabel">
                            订单总金额
                        </td>
                        <td width="*">
                            <div v-if="order.totalfee!=order.originfee">

                                <div  class="o-price">
                                    {{order.totalfee}}元
                                </div>
                            </div>

                            <div v-else class="o-price">
                                {{order.originfee}}元
                            </div>


                        </td>

                    </tr>

                    <tr v-if="huiyuan.jifen<0">
                        <td width="10%"  class="tlabel">
                            积分
                        </td>
                        <td width="*">
                            <div class="o-price">
                                <el-checkbox v-model="useJifen">使用积分</el-checkbox> {{huiyuan.jifen}}分
                            </div>

                        </td>

                    </tr>
                    <tr>
                        <td width="10%"  class="tlabel">
                            账户余额
                        </td>
                        <td width="*">
                            <div class="o-price">
                                {{huiyuan.yue}}¥
                            </div>

                        </td>

                    </tr>


                    <tr v-if="order.state==1">

                        <td width="10%"  class="tlabel">
                            支付密码:
                        </td>
                        <td  width="*">

                            <div style="width:220px;display:inline-block">
                                <el-input
                                    placeholder="请输入支付密码"
                                    v-model="paypwd"
                                    show-password
                                    clearable>
                                </el-input>
                            </div>

                        </td>

                    </tr>

                    <tr v-if="order.state==1">
                        <td colspan="4">
                            <el-button @click="payHandler(order.id)" type="danger">
                                <i class="el-icon-check"></i>确定付款
                            </el-button>

                        </td>

                    </tr>

                </table>
        </div>
        <div v-else style="min-height: 600px" class="wrap round-block">

            <div style="font-size:26px;padding:60px;">

                <i class="fa fa-check"></i>你已经成功支付订单金额，
                <router-link to="/huiyuan/shordermanager">查看</router-link>
            </div>

        </div>

        <bottom></bottom>

    </div>
</template>

<script>
    import  ss from "../util/SmartStorage";
    import Bottom from "./bottom";
    import Shead from "./shead";
    import loginmodal from "./loginmodal";
    export default {
        name: "payment",
        data(){
            return {
                paypwd: '',
                over:false,
                hostHead: this.$app.contextPath,
                order: {},
                useJifen: true,
                huiyuan:{},
                receaddress: null
            }
        },

        methods: {

            async getOrderInfo() {
                let {data: res} = await this.$http.post("admin/shorder/load", {id: this.$route.query.id});
                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                console.log("订单数据", res);
                this.order = res.data;

            },
            async getHuiyuanInfo() {

                let {data: res} = await this.$http.post("admin/huiyuan/load", {id: this.order.purchaser});

                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                this.huiyuan = res.data;
                console.log("huiyuan", this.huiyuan);
            },
            async getAddress(){

                let {data:res}=await this.$http.post("admin/receaddress/load",{id:this.order.addid});

                if (res.stateCode<0){
                    this.$message.error(res.des);
                    return ;
                }
                this.receaddress=res.data;
                console.log("receaddress",this.receaddress);
            },
            async payHandler(oid) {
                if (this.paypwd == "") {
                    this.$message.error("请输入支付密码");
                    return;
                }
                let {data: res} = await this.$http.post("admin/shorder/payment", {
                    id: this.$route.query.id,
                    usejifen:this.useJifen,
                    paypwd: this.paypwd
                });
                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                this.$message.success("支付成功");
                this.over=true;


            }

        },
        async created() {
            await this.getOrderInfo();
            await this.getAddress();
            await this.getHuiyuanInfo();
        },
        components:{Shead,Bottom}
    }
</script>

<style scoped>

</style>
